/* Reset default margin and padding */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1 {
  color: rgb(0, 0, 0);
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

.color {
  color: white;
}

/* Navigation styles */
nav {
  background-color: #000;
  padding: 31px;
  height: 645px;
  width: 232px;
  display: flex;
  position: sticky;
  top: 0;
  flex-direction: column-reverse;
  justify-content: space-between;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li {
  display: inline;
  margin-right: 20px;
}

a {
  text-decoration: none;
  color: #fff;
}

a:hover {
  color: #ccc;
}

/* Hamburger menu styles */
.hamburger {
  cursor: pointer;
  padding: 10px;
}

.hamburger span {
  display: flex;
  width: 20px;
  height: 3px;
  background-color: #fff;
  margin-bottom: 5px;
}

.show {
  display: flex;
  flex-direction: column;
  gap: 62px;
  margin-top: 20px;
  align-items: flex-start;
  justify-content: center;
}
.hide {
  display: none;
}

/* Layout */

.flex {
  display: flex;
  margin: 0 auto;
}

.two {
  text-align: center;
}

/* Media query for responsive design */
@media only screen and (max-width: 768px) {
  .hamburger {
    display: block;
  }
  .flex {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  nav {
    height: auto;
    width: 100%;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
      position: unset;
  }
  .two {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
    .show {
    position: absolute;
    top: 85px;
    right: 0px;
    background: black;
    width: 39%;
    z-index: 1010;
    border-radius: 5px;
    align-items: center;
  }
  a {
    color: white;
  }
  li {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 320px) {
  .hamburger {
    display: block;
  }
  .flex {
    width: 100%;
  }
  nav {
    height: auto;
    width: 100%;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    position: unset;
  }
  .two {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .show {
    position: absolute;
    top: 85px;
    right: 0px;
    background: black;
    width: 39%;
    z-index: 1010;
    border-radius: 5px;
    align-items: center;
  }
  a {
    color: white;
  }
  li {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}
